// main container with and position for adding product
.post-job-container {
  align-self: center;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;

  // heading styles
  .heading {
    font-size: 3.5rem;
    padding: 2%;
    color: navy;
  }

  // form styles of add product
  form {
    width: 80%;
    margin: 0px;
    padding: 0px;
    margin-bottom: 40px;

    .input-container {
      display: flex;
      flex-direction: column;
      margin: 3% 0%;
      font-size: 1.4rem;
      padding: 1% 0%;
      color: navy;

      // input tag styles
      input {
        height: 1.8rem;
        border: 2px solid navy;
        padding: 1%;
        font-size: 1.2rem;
        border-radius: 10px;
      }

      input:focus {
        outline: none;
        border: 2.5px solid #d66228;
      }

      textarea {
        height: 6rem;
        border: 2px solid navy;
        padding: 1%;
        font-size: 1.2rem;
        border-radius: 10px;
      }

      textarea:focus {
        outline: none;
        border: 2.5px solid #d66228;
      }

      .add-skills {
        border: 0.4px solid blue;
        width: 127px;
        text-align: center;
        cursor: pointer;
        padding: 0.5%;
        margin: 1% 4px 0px 0px;
        background-image: linear-gradient(to right, #031263, #0b1e88, #001aa8);
        color: white;
      }

      .core-skills-list {
        padding: 0px;
        margin: 0px;
        li {
          margin: 6px 40px;
        }
      }
    }
    // submit button styles
    button {
      padding: 1.5%;
      width: 42%;
      color: white;
      font-weight: bold;
      font-size: 1.5rem;
      background-image: linear-gradient(to right, #031263, #0b1e88, #001aa8);
      box-shadow: 0px 0px 2px 2px #423a3a;
      border: none;
      cursor: pointer;
      border-radius: 10px;
    }

    button:focus {
      outline: none;
      background-image: linear-gradient(to right, #031263, #0b1e88, #001aa8);
      box-shadow: 0px 0px 2px 2px #000000;
    }
  }
}
